import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import Input from '@material-ui/core/Input';
import MenuItem from '@material-ui/core/MenuItem';
import {UploadEditorImg} from '../utilities/utilFun'
import {updateAttachment,updateProduct,snackbar} from './../actions'
import {connect} from 'react-redux';
import {store} from '../App.js'
import IconButton from '@material-ui/core/IconButton';
import AddIcon from '@material-ui/icons/Add';
import DeleteIcon from '@material-ui/icons/Delete';
import Button from '@material-ui/core/Button'
import Fab from '@material-ui/core/Fab';
import axios from 'axios'
import  Hidden from '@material-ui/core/Hidden'
import Skeleton from '@material-ui/lab/Skeleton';
import Switch from '@material-ui/core/Switch'
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'

import {

    withRouter
  } from "react-router-dom";
class CreateProduct extends Component {
  constructor(props) {
    super(props);
this.state={
  Total:0,
  tax:0,
  shipping:0,
  subtotal:0,
  dataOrder:[],
  order:null,
}
  }


        componentDidMount(){

          var address=''
          axios.post('https://green-planet.herokuapp.com/public/admin/getOrders',{txnId:this.props.product}).then(res=>{
         if(res.data){
  console.log(res.data)
           const products=res.data[0].products
           var dataOrder=[]

           let Subtotal=0
           let price=0
           let tax=0
           let shipping=0
           products.map((data,i)=>{
             var rp=data.price/(1+(data.CGST+data.IGST+data.SGST)/100)
             Subtotal=Subtotal+(data.price*data.quantity)+(data.shipping*data.quantity)
             shipping=shipping+(data.shipping*data.quantity)
             price=price+rp
             tax=tax+(data.price-rp)
             dataOrder.push({title:data.product.title,sku:data.product.sku,quantity:data.quantity,shipping:data.shipping,tax:data.CGST+data.IGST+data.SGST,price:data.price})
           })
this.setState({Total:Subtotal,tax,shipping,subtotal:price,dataOrder,order:res.data})

         }




         })

         }

render(){


  return (
    <React.Fragment style={{backgroundColor:'grey'}}>
      <Grid container spacing={3} justify={'center'} style={{marginTop:'50px',marginBottom:'50px'}}>
      <Grid item xs={12}><Title>{'Order Details'}</Title></Grid>
      <Grid item xs={12}>
      <Paper >
            <Table >
              <TableHead>
                <TableRow>
                  <TableCell>Product</TableCell>
                  <TableCell >SKU</TableCell>
                        <TableCell >Qty.</TableCell>
                  <TableCell >Shipping</TableCell>
                  <TableCell >Price</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {this.state.dataOrder.map(data=>(

                  <TableRow key={data.sku}>
                    <TableCell>{data.title}</TableCell>
                    <TableCell >{data.sku}</TableCell>
                    <TableCell >{data.quantity}</TableCell>
                      <TableCell >{data.shipping}</TableCell>
                    <TableCell >{data.price}</TableCell>
                  </TableRow>
                ))}




              </TableBody>
            </Table>
          </Paper></Grid>
        <Grid xs={12}>
          <List >

                       <ListItem>
                         <ListItemText
                           primary="Shipping Address"
                           secondary={this.state.order?this.state.order[0].address.address:null}
                         />
                       </ListItem>
                       <ListItem>
                         <ListItemText
                           primary="Seller Address"
                           secondary={this.state.order?this.state.order[0].seller.address:null}
                         />
                       </ListItem>
                       <ListItem>
                         <ListItemText
                           primary={"Tax :"+this.state.tax.toFixed(2)}
                           secondary={'CGST + SGST +IGST'}
                         />
                       </ListItem>
                       <ListItem>
                         <ListItemText
                           primary={"Shipping Charges :"+this.state.shipping}

                         />
                       </ListItem>,
                       <ListItem>
                         <ListItemText
                           primary={"Total Amount: "+this.state.Total}
                           secondary={'inclusive of all taxes'}
                         />
                       </ListItem>

                   </List>
        </Grid>
        </Grid>
    </React.Fragment>

)

}
}


const mapStateToProps = state => {

    return {
      attachment:state.product.attachment,
      products:state.product.products,
      config:state.common.config
     }
};

export default withRouter(connect(mapStateToProps,{updateAttachment,updateProduct,snackbar})(CreateProduct));
